.search-overlay {
  /* Track */
  /* Handle */
  /* Handle on hover */
  overflow-y: hidden;
  overflow-x: hidden;
  z-index: 9999;
  position: absolute;
  top: 0;
  right: 12rem;
  visibility: hidden;
  opacity: 0;
  transform: scale(1.09);
  transition: all 0.3s;
  box-sizing: border-box;
  height: 0;
  min-height: 0rem;
  border-radius: 1rem;
  width: 0;
  padding-bottom: 10%;
}
.search-overlay::-webkit-scrollbar-track {
  background: transparent;
  border-radius: 10px;
}
