.btn-nav,
.btn-nav-left {
  position: relative;
  overflow: hidden;
  background: black;
  text-transform: uppercase;
  transition: all 0.35s;
  z-index: 1;
}

.btn-nav:before,
.btn-nav:after {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  top: -100%;
  left: 0;
  background: white;
  z-index: -1;
  transition: all 0.35s;
}

.btn-nav-left:before,
.btn-nav-left:after {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  top: 0;
  left: -100%;
  background: white;
  z-index: -1;
  transition: all 0.35s;
}

.btn-nav.current-menu-item:before,
.btn-nav.current-menu-item:after {
  background: black;
}
.btn-nav.current-menu-item:before a,
.btn-nav.current-menu-item:after a {
  color: white;
}
.btn-nav.current-menu-item:before:hover a,
.btn-nav.current-menu-item:after:hover a {
  color: white;
}

.btn-nav:before {
  opacity: 0.5;
}

.btn-nav:after {
  transition-delay: 0.2s;
}

.btn-nav:hover {
  color: black;
}
.btn-nav:hover a {
  color: black;
}

.btn-nav.btn-nav.current-menu-item:hover {
  color: white;
}

.btn-nav:hover:before,
.btn-nav:hover:after {
  top: 0;
}

.btn-nav-left:hover:before,
.btn-nav-left:hover:after {
  left: 0;
}

.btn-wk {
  position: relative;
  transition: all 0.5s;
  font-weight: 800;
  font-family: "Montserrat", sans-serif;
  text-transform: uppercase;
}
.btn-wk:active {
  transform: translateY(-1px);
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);
}
.btn-wk:hover {
  transform: translateY(-4px);
  -webkit-box-shadow: 0px 47px 46px 10px rgba(0, 0, 0, 0.71);
  -moz-box-shadow: 0px 47px 46px 10px rgba(0, 0, 0, 0.71);
  box-shadow: 0px 47px 46px 10px rgba(0, 0, 0, 0.71);
}
.btn-wk:hover {
  text-decoration: none;
}
.btn-wk::after {
  content: "";
  display: inline-block;
  height: 100%;
  width: 100%;
  border-radius: inherit;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  transition: all 0.4s;
  background-color: inherit;
}
.btn-wk:hover::after {
  transform: scaleX(1.4) scaleY(1.6);
  opacity: 0;
}
.btn-wk__alt {
  border: none;
  color: #fff;
  text-align: center;
  font-size: 32px;
  padding: 16px;
  width: 220px;
  transition: all 0.5s;
  cursor: pointer;
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
  width: 100%;
}
.btn-wk__alt:after {
  content: "";
  position: absolute;
  opacity: 0;
  top: 14px;
  right: -20px;
  transition: 0.5s;
}
.btn-wk__alt:hover {
  padding-right: 24px;
  padding-left: 8px;
}
.btn-wk__alt:hover:after {
  opacity: 1;
  right: 10px;
}

