*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

/* ======================================== */
/* ================scroolbar=================== */
/* ======================================== */
/* width */
::-webkit-scrollbar {
  width: 1vw;
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent;
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  @apply bg-primary;
  border-radius: 10px;
  transition: background-color 0.8s;
}

::-webkit-scrollbar-corner {
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  --tw-bg-opacity: 1;
  @apply bg-blue-900;
}

/* ======================================== */
/* ================scroolbar=================== */
/* ======================================== */
html {
  /* this defines what one rem is */
  font-size: 62.5%;
}

